<template>
	<div id="cityDispatching">
		<div class="header">
			<van-row gutter="20">
        <van-col span="4" style="padding-right: 0px !important;padding-left: 0px;">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #8b8b8b !important;vertical-align: middle;"></i>
        </van-col>
				<van-col span="16" class='search font_12' style="background:#ffffff;">
          <van-search v-model="value" placeholder="搜索" @search="getList"/>
        </van-col>
        <van-col span="3" style="padding-left:0px !important;padding-right: 0px;">
          <img src="static/img/icon_suaxin_mmersf.png" style="vertical-align: middle;width: 20px;" @click="$router.replace('cityDispatching')"/>
        </van-col>
			</van-row>
		</div>
    <div class="body_box" style="padding: 5px">
      <div class="background_color_ffffff border_top_eeeeee" style="padding: 9px;" v-for="(item,index) in list">
        <img :src="item.user.userimg||'static/img/img_tx_mmersf.png'" width="32px" style="vertical-align: text-top;float: left;margin-right: 6px;"/>
        <span class="font_14">{{item.user.username}}</span>
        <br>
        <span>
          <img src="static/img/vipp.png"/>
          <span class="font_12 color_fcba62">会员</span>
          <span class="float_right font_12 color_8b8b8b">发布时间:{{item.addtime}}</span>
        </span>
        <div style="margin-top: 10px">
          <div class="font_14">{{item.title}}</div>
          <div class="font_12 color_8b8b8b" style="padding: 5px 0px;">
            {{item.content}}
            <span class="font_12 color_c10804 float_right" @click="toDispatchingInfo(item)">查看全文>></span>
          </div>
        </div>
      </div>
    </div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
        list:[],
        value:'',
			}
		},
    mounted() {
      this.getList();
    },
		methods: {
      getList(){
        let $this = this;
        let obj = {
          search:this.value,
          type: 4,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecitylist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.list = rpdata.data.data.data;
          }
        })
        .catch(function(err) {})
      },
      toDispatchingInfo(dataMap){
        this.$store.commit('setDispatchingInfo', dataMap);
        this.$router.push('dispatchingInfo');
      },
		}
	}
</script>

<style>
  #cityDispatching .body_box{
    margin-top: 50px;
  }
  #cityDispatching .van-search input {
    color: #8b8b8b;
  }
  #cityDispatching .typeList {
    text-align: center;
  }
  #cityDispatching .typeList span {
    font-size: 14px;
    color: rgb(102, 102, 102)
  }
  #cityDispatching .typeImg {
    display: block;
    width: 75%;
    margin: 10px auto;
  }
	#cityDispatching {
		width: 100%;
	}
	#cityDispatching .header {
		height: 44px;
		line-height: 44px;
		box-shadow: 0px 2px 10px lightgrey;
		overflow: hidden;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 99;
		background-color: white;
	}
	#cityDispatching .header img {
		vertical-align: text-top;
	}
	#cityDispatching .search {
		border: 1px solid lightgray;
		border-radius: 30px;
		margin-top: 6px;
	}
	#cityDispatching .header .van-col {
		text-align: center;
	}
	#cityDispatching .van-search {
    padding: 0px;
  }
	#cityDispatching .van-search {
		background-color: transparent!important;
	}
	#cityDispatching .swiper img {
		margin: 0 auto;
		display: block;
	}
	#cityDispatching .nav img {
		width: 100%;
		height: auto;
	}
	#cityDispatching .navCon span {
		font-size: 13px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: white;
	}
	#cityDispatching .rightText div {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 16px;
	}
	#cityDispatching .footer .van-col div {
		text-align: center;
		font-size: 14px;
	}
	#cityDispatching .swiperTwo .van-swipe__indicators{
		display: none;
	}
	#cityDispatching .swiperTwo{
    margin-top: 44px;
	}
  #cityDispatching .van-field__control {
    font-size: 12px;
  }
</style>



